import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';

class Users extends Component {
  constructor() {
    super()
    

    this.state = {
      users :[]
    
    };
    this.onChange = this.onChange.bind(this);
  }

  componentDidMount(){
    this.getUsers();
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  getUsers=async()=>{
    await axios.get('/getusers').then(res=>{
      this.setState({
        users:res.data
      })
    })
  }
  updateUser(rowIndex,row){
      axios.post('/updateuser',{
        email: row.email,
      }).then(res =>{
       var status=res.data.status;
       if(!status.localeCompare('Successfull')){
         this.getUsers();
       }
       else{
         this.getDeliveries();
         alert('Failed')
       }
     })
    
    
  }
  updateUserRole(rowIndex,row){
    if(!rowIndex.localeCompare('Admin')&&row.userID!==1){
      axios.post('/updateuserrole',{
        role:'Member',
        email: row.email,
      }).then(res =>{
       var status=res.data.status;
       if(!status.localeCompare('Successfull')){
         this.getUsers();
       }
       else{
         this.getDeliveries();
         alert('Failed')
       }
     })
    }
    else{
      axios.post('/updateuserrole',{
        role:'Admin',
        email: row.email,
      }).then(res =>{
       var status=res.data.status;
       if(!status.localeCompare('Successfull')){
         this.getUsers();
       }
       else{
         this.getDeliveries();
         alert('Failed')
       }
     })
    }
  
  
}

  cellButton(rowIndex,row) {
    if(!rowIndex.localeCompare('Inactive')){
      return (
        <Button block color="warning"
        onClick={()=>{this.updateUser(rowIndex,row)}} >
          { rowIndex }
        </Button>
        )
    }
    else if(!rowIndex.localeCompare('Active')){
      return (
        <Button block color="success" >
          { rowIndex }
        </Button>
        )
    }
  	
  }
  cellButtonRole(rowIndex,row) {
    if(!rowIndex.localeCompare('Admin')){
      return (
        <Button block color="info"
        onClick={()=>{this.updateUserRole(rowIndex,row)}} >
          { rowIndex }
        </Button>
        )
    }
    else if(!rowIndex.localeCompare('Member')){
      return (
        <Button block color="warning" 
        onClick={()=>{this.updateUserRole(rowIndex,row)}}>
          { rowIndex }
        </Button>
        )
    }
  	
  }
    //formate date in delivery table
    dateFormater(cell , row){
      var stDate = new Date(cell);
      var date = stDate.getFullYear() + '-' + (stDate.getMonth() + 1) + '-' + stDate.getDate();
       return `${date}`
     }

     onAfterDeleteRow=(rowKeys)=> {
      axios.post('/deleteuser',{
        id:rowKeys
      }).then(res =>{
        var status=res.data.status;
        if(!status.localeCompare('Successfull')){
          alert('Successfully Deleted');
          this.getUsers();
        }
        else{
          alert('Failed To Delete')
        }
      })
      
    }

    
    selectRowProp={
      mode:'radio',
      bgColor:'yellow'
    }
    
    options = {
      afterDeleteRow: this.onAfterDeleteRow // A hook for after droping rows.
    
    };
    se
  render() {
    return (
      <div className="animated fadeIn">
        <Container>
        <Row>
            <Card>
              <CardHeader>
                <strong>Company</strong>
              </CardHeader>
              <CardBody>
              <div>
                <BootstrapTable data={this.state.users } height='375' scrollTop={'Bottom'} search={true}
                selectRow={this.selectRowProp} deleteRow={true} options={this.options}
                tableStyle={ { border: '#0000FF 2.5px solid' } }>
     
                <TableHeaderColumn dataField='userID' 
                     isKey >userId</TableHeaderColumn>
                <TableHeaderColumn dataField='first_name'
                      >User Name</TableHeaderColumn>
               <TableHeaderColumn dataField='email'
                      >Email</TableHeaderColumn>
                <TableHeaderColumn dataField='role'
                      dataFormat={this.cellButtonRole.bind(this)}>Role</TableHeaderColumn>
                <TableHeaderColumn  dataField='Status' dataFormat={this.cellButton.bind(this)}
                      >Status</TableHeaderColumn>
                <TableHeaderColumn  dataField='created' dataFormat={this.dateFormater.bind(this)}
                      >Reg Date</TableHeaderColumn>

                </BootstrapTable>
 
            </div>
              </CardBody>
            </Card>
         
        
        </Row>  
        </Container>
       
        
      </div>
    );
  }
  
}

export default Users;
